import type { App,Plugin } from 'vue'
import ZButton from './src/buttons.vue'

ZButton.name = 'ZButton';
// 导出组件类型
export type ButtonInstance = InstanceType<typeof ZButton>
export type SFCWithInstall<T> = T & Plugin;

// 导出组件事件类型
export type { ButtonEmits, ButtonProps } from './src/types';

 ZButton.install=(app: App)=>{
    app.component('ZButton', ZButton);
  }

  export {ZButton}

export default ZButton;